/* Modern Navbar with Glass Effect */
.navbar {
  width: 100%;
  height: 80px;
  background: linear-gradient(135deg, rgba(45, 52, 75, 0.95) 0%, rgba(28, 34, 50, 0.98) 100%);
  border-bottom: 1px solid rgba(85, 126, 214, 0.15);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  position: sticky;
  top: 0;
  z-index: 1000;
  transition: all 0.3s ease;
}

.navbar:hover {
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
}

.navbar .leftSide {
  flex: 40%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 5%;
}

.navbar .leftSide img {
  width: 50px;
  height: auto;
  transition: transform 0.3s ease;
}

.navbar .leftSide img:hover {
  transform: scale(1.1);
}

.navbar .rightSide {
  flex: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 5%;
}

.navbar a {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  margin: 0 18px;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 0.5px;
  position: relative;
  transition: all 0.3s ease;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.navbar a:hover {
  color: #ffffff;
  transform: translateY(-2px);
}

/* Animated underline effect */
.navbar a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -4px;
  left: 0;
  background-color: #3a86ff;
  transition: width 0.3s ease;
}

.navbar a:hover::after {
  width: 100%;
}

.navbar .rightSide button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.navbar .rightSide button:hover {
  transform: scale(1.1);
}

.navbar .rightSide svg {
  font-size: 28px;
  color: rgba(255, 255, 255, 0.9);
}

/* Mobile menu styles */
.navbar #open {
  padding-left: 0px;
}

.navbar #open img {
  display: none;
}

.navbar #close img {
  display: inherit;
}

.navbar .hiddenLinks {
  background: rgba(43, 49, 69, 0.98);
  backdrop-filter: blur(10px);
  position: absolute;
  top: 80px;
  right: 0;
  width: 200px;
  padding: 20px;
  border-radius: 0 0 0 10px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.navbar #open .hiddenLinks {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.navbar #close .hiddenLinks {
  display: none;
}

.navbar #open .hiddenLinks a {
  width: 100%;
  margin: 10px 0;
  padding: 8px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.navbar #open .hiddenLinks a:last-child {
  border-bottom: none;
}

/* Active link indicator */
.navbar a.active {
  color: #3a86ff;
}

.navbar a.active::after {
  width: 100%;
  background-color: #3a86ff;
}

/* CTA Button */
.navbar .cta-button {
  background: linear-gradient(90deg, #3a86ff 0%, #0057e7 100%);
  border-radius: 24px;
  padding: 8px 20px;
  margin-left: 15px;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(58, 134, 255, 0.3);
}

.navbar .cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(58, 134, 255, 0.4);
}

.navbar .cta-button::after {
  display: none;
}

/* Responsive breakpoints */
@media only screen and (max-width: 1024px) {
  .navbar .leftSide {
    flex: 30%;
  }
  
  .navbar .rightSide {
    flex: 70%;
  }
}

@media only screen and (max-width: 900px) {
  .navbar a {
    margin: 0 12px;
    font-size: 0.9rem;
  }
  
  .navbar .leftSide {
    padding-left: 4%;
  }
  
  .navbar .rightSide {
    padding-right: 4%;
  }
  
  .navbar .cta-button {
    padding: 7px 16px;
  }
}

@media only screen and (max-width: 768px) {
  .navbar {
    height: 70px;
  }
  
  .navbar .leftSide img {
    width: 45px;
  }
  
  .navbar .rightSide a:not(.cta-button) {
    display: none;
  }
  
  .navbar .rightSide .cta-button {
    display: block;
  }
  
  .navbar .rightSide {
    justify-content: flex-end;
  }
  
  .navbar .rightSide button {
    display: block;
    margin-left: 15px;
  }
  
  .navbar .hiddenLinks {
    top: 70px;
  }
}



