/* Theme toggle button: always visible */
.theme-toggle {
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.9); /* White-ish on dark */
  font-size: 1.5rem;
  cursor: pointer;
  margin-left: 15px;
  transition: color 0.3s ease;
}

/* Adjust color for light mode */
body:not(.dark-mode) .theme-toggle {
  color: #333;
}

.theme-toggle:hover,
.theme-toggle:focus-visible {
  color: #3a86ff;
  outline: none;
}

/* Mobile menu toggle: hidden by default */
.mobile-only {
  display: none;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.8rem;
  cursor: pointer;
  margin-left: 15px;
  transition: color 0.3s ease;
}

/* Show mobile menu toggle only on smaller screens */
@media (max-width: 768px) {
  .mobile-only {
    display: inline-block;
  }

  /* Hide desktop links on small screens */
  .navbar .rightSide > a:not(.cta-button) {
    display: none;
  }

  /* Show hiddenLinks only when menuOpen */
  .hiddenLinks {
    display: none;
    position: absolute;
    top: 80px; /* adjust as needed */
    right: 0;
    background: rgba(43, 49, 69, 0.98);
    backdrop-filter: blur(10px);
    padding: 20px;
    border-radius: 0 0 0 10px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    flex-direction: column;
    z-index: 1000;
  }
  
  .hiddenLinks.open {
    display: flex;
  }
}

/* Optional: add pointer cursor to links */
.navbar a {
  cursor: pointer;
}

/* Hidden by default on all screens */
.hiddenLinks {
  display: none;
}

/* Show only on mobile when open */
@media (max-width: 768px) {
  .hiddenLinks.open {
    display: flex; /* or block, flex works well for column layout */
    flex-direction: column;
    position: absolute;
    top: 70px; /* adjust to navbar height */
    right: 0;
    background: rgba(43, 49, 69, 0.98);
    backdrop-filter: blur(10px);
    padding: 20px;
    border-radius: 0 0 0 10px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    z-index: 1000;
  }
}

/* Button Styles */
.navbar .rightSide button {
  background-color: transparent;
  border: 2px solid rgba(255, 255, 255, 0.9); /* Border color for light mode */
  color: rgba(255, 255, 255, 0.9); /* Text color for light mode */
  cursor: pointer;
  padding: 8px 12px; /* Add some padding */
  border-radius: 5px; /* Rounded corners */
  transition: all 0.3s ease;
}

/* Dark Mode Button Styles */
body.dark-mode .navbar .rightSide button {
  border-color: rgba(255, 255, 255, 0.5); /* Lighter border for dark mode */
  color: rgba(255, 255, 255, 0.9); /* Keep text color white */
}

/* Light Mode Button Styles */
body:not(.dark-mode) .navbar .rightSide button {
  border-color: rgba(0, 0, 0, 0.2); /* Darker border for light mode */
  color: rgba(0, 0, 0, 0.9); /* Dark text color for light mode */
}

/* Button Hover Effects */
.navbar .rightSide button:hover {
  background-color: rgba(255, 255, 255, 0.1); /* Light hover effect for light mode */
}

body.dark-mode .navbar .rightSide button:hover {
  background-color: rgba(255, 255, 255, 0.2); /* Dark hover effect for dark mode */
}

/* Active Button State */
.navbar .rightSide button:active {
  transform: scale(0.95); /* Slightly shrink on click */
}




